/* src/TasksPage.css */

/* הסבר רספונסיבי ומודרני */
.tasks-explanation {
    margin-bottom: 20px;
    background: linear-gradient(135deg, #e3f2fd, #90caf9);
    padding: 20px;
    border-radius: 12px;
    font-size: 1.1rem;
    line-height: 1.7;
    text-align: right;
    color: #333;
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
    transition: background 0.3s ease;
    max-width: 1510px;
  }
  
  .tasks-explanation:hover {
    background: linear-gradient(135deg, #bbdefb, #42a5f5);
  }
  
  .tasks-explanation p {
    margin-bottom: 1rem;
  }
  
  .tasks-explanation ul {
    list-style-type: disc;
    margin-left: 0;
    margin-right: 20px;
    padding-right: 20px;
    direction: rtl; /* מוודא שהכיווניות תהיה מימין לשמאל */
  }
  
  .tasks-explanation ul li {
    margin-bottom: 0.5rem;
    text-align: right; /* יישור הטקסט מימין */
  }
  
  .tasks-page h1 {
    text-align: center;
    color: #1565c0;
    font-size: 2rem;
    margin-bottom: 1.5rem;
  }

  .add-task-button {
    background-color: #4CAF50;
    color: white;
    border: none;
    padding: 12px 24px;
    font-size: 16px;
    cursor: pointer;
    border-radius: 5px;
    margin-bottom: 20px;
    box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
    transition: background-color 0.3s ease;
    margin-top: 40px;
}

.add-task-button:hover {
    background-color: #45a049;
}
