/* MonthlyExpensesTotal.css */

.expenses-container {
  background-color: #ffffff;
  border-radius: 12px;
  padding: 20px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  text-align: center;
  transition: transform 0.3s ease-in-out;
  margin: 20px 0;
}

.expenses-container:hover {
  transform: translateY(-5px);
}

.expenses-container h3 {
  font-size: 1.8rem;
  color: #007bff;
  margin-bottom: 15px;
}

.expenses-container p {
  font-size: 2rem;
  color: #333;
  margin: 0;
  font-weight: bold;
}

.expenses-container p::before {
  content: '₪';
  margin-right: 5px;
  color: #00d4ff;
  font-size: 1.8rem;
  vertical-align: middle;
}

/* עיצוב רספונסיבי */
@media (max-width: 768px) {
  .expenses-container {
    padding: 15px;
  }

  .expenses-container h3 {
    font-size: 1.6rem;
  }

  .expenses-container p {
    font-size: 1.8rem;
  }
}

@media (max-width: 480px) {
  .expenses-container h3 {
    font-size: 1.4rem;
  }

  .expenses-container p {
    font-size: 1.6rem;
  }
}
