/* עיצוב כללי לטופס */
.private-task-form form {
  max-width: 600px;
  margin: 0 auto;
  padding: 1.5rem;
  border: 1px solid #e0e0e0;
  border-radius: 8px;
  background-color: #ffffff;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  direction: rtl; /* יישור לימין */
  transition: all 0.3s ease-in-out; /* מעבר רך */
}

/* עיצוב רספונסיבי למסכים קטנים */
@media (max-width: 768px) {
  .private-task-form form {
    max-width: 90%; /* התאמה לרוחב 90% במסכים קטנים */
    padding: 1rem;
  }

  .private-task-form input[type="text"],
  .private-task-form textarea,
  .private-task-form input[type="date"],
  .private-task-form select {
    font-size: 0.9rem;
    padding: 0.65rem;
  }

  .private-task-form label {
    font-size: 0.9rem;
  }

  .private-task-form button[type="submit"] {
    padding: 0.65rem 1rem;
    font-size: 0.9rem;
  }
}

/* עיצוב רספונסיבי למסכים קטנים מאוד */
@media (max-width: 480px) {
  .private-task-form form {
    padding: 0.75rem;
    max-width: 95%; /* הקטנה נוספת לטופס במסכים קטנים מאוד */
  }

  .private-task-form input[type="text"],
  .private-task-form textarea,
  .private-task-form input[type="date"],
  .private-task-form select {
    font-size: 0.85rem;
    padding: 0.6rem;
  }

  .private-task-form button[type="submit"] {
    padding: 0.6rem 0.9rem;
    font-size: 0.85rem;
  }
}

/* עיצוב לשדות של הטופס */
.private-task-form div {
  margin-bottom: 1.5rem;
}

/* עיצוב לתוויות */
.private-task-form label {
  display: block;
  font-weight: bold;
  margin-bottom: 0.5rem;
  color: #333; /* צבע טקסט כהה */
}

/* עיצוב לשדות טקסט */
.private-task-form input[type="text"],
.private-task-form textarea,
.private-task-form input[type="date"],
.private-task-form select {
  width: 100%;
  padding: 0.75rem;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
  font-size: 1rem;
  font-family: Arial, sans-serif;
  direction: rtl; /* יישור לימין */
}

.private-task-form textarea {
  height: 120px;
  resize: vertical;
}

/* עיצוב כפתור השליחה */
.private-task-form button[type="submit"] {
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 4px;
  padding: 0.75rem 1.5rem;
  font-size: 1rem;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.private-task-form button[type="submit"]:hover {
  background-color: #0056b3;
}

/* תצורת הכפתורים בתחתית הטופס */
.private-task-form .modal-actions {
  display: flex;
  justify-content: space-between;
  margin-top: 20px;
}

.private-task-form .modal-actions button {
  padding: 10px 20px;
  border: none;
  cursor: pointer;
  border-radius: 5px;
}

.private-task-form .modal-actions .submit-button {
  background-color: #4CAF50;
  color: white;
}

.private-task-form .modal-actions .submit-button:hover {
  background-color: #45a049;
}

.private-task-form .modal-actions .close-button {
  background-color: #f44336;
  color: white;
}

.private-task-form .modal-actions .close-button:hover {
  background-color: #e53935;
}
