/* עיצוב כללי לטופס */
form {
    max-width: 600px;
    margin: 0 auto;
    padding: 1.5rem;
    border: 1px solid #e0e0e0;
    border-radius: 8px;
    background-color: #ffffff;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    direction: rtl; /* יישור לימין */
}

/* עיצוב לשדות של הטופס */
div {
    margin-bottom: 1.5rem;
}

/* עיצוב לתוויות */
label {
    display: block;
    font-weight: bold;
    margin-bottom: 0.5rem;
    color: #333; /* צבע טקסט כהה */
}

/* עיצוב לשדות טקסט */
input[type="text"],
textarea,
input[type="date"],
select {
    width: 100%;
    padding: 0.75rem;
    border: 1px solid #ccc;
    border-radius: 4px;
    box-sizing: border-box;
    font-size: 1rem;
    font-family: Arial, sans-serif;
    direction: rtl; /* יישור לימין */
}

/* עיצוב לאזורי טקסט */
textarea {
    height: 120px;
    resize: vertical;
}

/* עיצוב לכפתור השליחה */
button[type="submit"] {
    background-color: #007bff; /* צבע כחול */
    color: white;
    border: none;
    border-radius: 4px;
    padding: 0.75rem 1.5rem;
    font-size: 1rem;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

/* שינוי צבע הכפתור בעת ריחוף */
button[type="submit"]:hover {
    background-color: #0056b3; /* צבע כחול כהה יותר */
}

/* עיצוב רספונסיבי למסכים קטנים יותר */
@media (max-width: 768px) {
    form {
        max-width: 100%;
        padding: 1rem;
        box-shadow: none;
    }

    input[type="text"],
    textarea,
    input[type="date"],
    select {
        font-size: 0.9rem;
        padding: 0.65rem;
    }

    label {
        font-size: 0.9rem;
    }

    button[type="submit"] {
        padding: 0.65rem 1rem;
        font-size: 0.9rem;
    }
}

/* עיצוב רספונסיבי למסכים קטנים מאוד */
@media (max-width: 480px) {
    form {
        padding: 0.75rem;
    }

    input[type="text"],
    textarea,
    input[type="date"],
    select {
        font-size: 0.85rem;
        padding: 0.6rem;
    }

    button[type="submit"] {
        padding: 0.6rem 0.9rem;
        font-size: 0.85rem;
    }
}

/* תצורת הכפתורים בתחתית הטופס */
.modal-actions {
    display: flex;
    justify-content: space-between; /* מיקום כפתור שלח בצד ימין וכפתור סגור בצד שמאל */
    margin-top: 20px;
  }
  
  .modal-actions button {
    padding: 10px 20px;
    border: none;
    cursor: pointer;
    border-radius: 5px;
  }
  
  .modal-actions .submit-button {
    background-color: #4CAF50;
    color: white;
  }
  
  .modal-actions .submit-button:hover {
    background-color: #45a049;
  }
  
  .modal-actions .close-button {
    background-color: #f44336;
    color: white;
  }
  
  .modal-actions .close-button:hover {
    background-color: #e53935;
  }
  