/* TaskSummary.css */

/* מיכל כללי עבור הסיכום */
.summary-container {
  background-color: #f8f9fa;
  border-radius: 12px;
  padding: 20px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
  margin: 20px 0;
  text-align: center;
  transition: transform 0.3s ease-in-out;
}

.summary-container:hover {
  transform: translateY(-5px);
}

/* כותרת הסיכום */
.summary-container h3 {
  font-size: 1.8rem;
  color: #007bff;
  margin-bottom: 15px;
}

/* עיצוב עבור המספרים */
.summary-container p {
  font-size: 1.4rem;
  color: #333;
  font-weight: bold;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10px;
}

/* אייקונים עבור המשימות */
.task-icon {
  width: 30px;
  height: 30px;
  background-size: cover;
  display: inline-block;
}

.not-completed-icon {
  background-image: url('https://cdn-icons-png.flaticon.com/512/1632/1632670.png'); /* אייקון למשימות שלא בוצעו */
}

.completed-icon {
  background-image: url('https://cdn-icons-png.flaticon.com/512/190/190411.png'); /* אייקון למשימות שבוצעו */
}

/* עיצוב רספונסיבי */
@media (max-width: 768px) {
  .summary-container h3 {
    font-size: 1.6rem;
  }

  .summary-container p {
    font-size: 1.2rem;
  }
}

@media (max-width: 480px) {
  .summary-container h3 {
    font-size: 1.4rem;
  }

  .summary-container p {
    font-size: 1.1rem;
  }
}
